
.navbar {
  display: flex;
  justify-content: space-between; 
  align-items: center; 
  padding: 10px 20px; 
  background-color: #f0f0f0;
  text-decoration: none;
}

ul {
  display: flex; 
  margin: 0;
  padding: 0; 
}

li {
  list-style-type: none;
  text-decoration: none;
  margin-left: 40px;
  
}

img {
  height: 30px; 
  text-decoration: none;
}

.navbar a{
  text-decoration: none;
  
}
.Link{
  color: rgba(34, 113, 125, 0.608);
}
.navbar a {
  text-decoration: none;
}

.navbar a:visited {
  text-decoration: underline; /*Underline visited links*/
  
   
                                 
}

.navbar a:focus, .navbar a.active {
  
  text-decoration: underline; /* Underline focused or active links */
  
}

